import React, { useEffect, Component, ReactElement, FC, useState } from 'react';
import ReactDOM from "react-dom";
import './../App.css';
import { Link } from "react-router-dom";
import Ipage from '../interfaces/page';
import logging from '../config/logging';
import ApyLogoDark from "./../assets/apy-logo-dark.png";
import CourseService from '../services/course';
import { useParams, useHistory } from "react-router-dom";
import { Button, Checkbox, FormControlLabel, IconButton, List, ListItem, ListItemAvatar, ListItemText } from '@mui/material';
import { Delete, Download, Visibility } from '@mui/icons-material';

interface Iprops {
    match: any;
}
interface IState {
    is_loading: Boolean;
    course?: any;
    id: any;
};

type ChildProps = {
    course: any | {
        title: string;
        tags: any;
    }
}

type SubjectType = {
    id: string;
    code: string;
    name: string;
    short_name: string;
}

const OldPaperCard: FC<{ papers: any, year: any, sem: any }> = ({ papers, year, sem }): ReactElement => {
    return <div>
        {
            (papers && papers.length) ?
                <div>
                    <h1 className="px-2 order-1 text-gray-900 text-base font-bold tracking-tight">{year} - {sem}</h1>
                    {
                        papers.map((paper: any, ind: any) =>
                            <ListItem key={ind} secondaryAction={
                                <div>
                                    {
                                        (paper?.link !== undefined && paper?.link !== "") &&
                                        <a href={paper?.link}>
                                            <IconButton size="small" edge="end" aria-label="delete"> <Download /> </IconButton>
                                        </a>
                                    }
                                </div>
                            }>
                                <ListItemText primary={paper?.name} secondary={`${paper?.code} - ${paper?.short_name}`} />
                            </ListItem>
                        )
                    }
                </div> : <React.Fragment></React.Fragment>
        }
    </div>
}

const CourseCard: FC<ChildProps> = ({ course }): ReactElement => {

    const [currentTag, setCurrentTag] = useState(undefined);
    const [currentSubjects, setCurrentSubjects] = useState<(string | number)[]>([]);
    const [viewAll, setViewAll] = useState(true);
    const params: any = useParams();
    const history: any = useHistory();

    useEffect(() => {
        setCurrentTag(params?.tag);
        let subjects = params?.subject;
        subjects = subjects ? subjects.split(",") : [];
        setCurrentSubjects(subjects);
    }, [params])

    const getCurrentTag = () => {
        return course?.tags.find((val: any) => val?.id == currentTag);
    }

    const getCurrentSubject = () => {
        if (getCurrentTag()) {
            return undefined;
        } else {
            return Array.isArray(getCurrentTag()?.subjects) && getCurrentTag()?.subjects.find((val: SubjectType) => currentSubjects && currentSubjects.includes(val.id));
        }
    }

    const changeSubject = (tag: string, subjectId: string) => {
        ;
        let subjects = currentSubjects;
        if (currentSubjects.includes(subjectId)) {
            if (subjects.length > 1) {
                let index = currentSubjects.indexOf(subjectId);
                subjects.splice(index, 1);
            }
        } else {
            subjects.push(subjectId)
        }
        console.log(subjects.length, subjects)
        if (subjects.length === 0) setViewAll(true)
        else setViewAll(false)
        history.push({ pathname: `/course/${course?.id}/${tag}/${subjects.join(",")}` });
    }

    const changeViewAll = () => {
        setViewAll(viewAll ? false : true);
    }

    const getSyllabus = () => {
        if (currentTag && getCurrentTag() && getCurrentTag()?.syllabus && Array.isArray(getCurrentTag()?.syllabus)) {
            return getCurrentTag()?.syllabus.filter((syllabus: any) => viewAll || currentSubjects.includes(syllabus?.subject_id))
        } else {
            return [];
        }
    }

    const getOldPapers = () => {
        if (currentTag && getCurrentTag() && getCurrentTag()?.old_papers && Array.isArray(getCurrentTag()?.old_papers)) {
            return getCurrentTag()?.old_papers
        } else {
            return [];
        }
    }

    const getBooks = () => {
        if (currentTag && getCurrentTag() && getCurrentTag()?.books && Array.isArray(getCurrentTag()?.books)) {
            return getCurrentTag()?.books
        } else {
            return [];
        }
    }

    const getOldYearPapers = (year: any, sem: any) => {
        let oldPapers = getOldPapers();
        if (oldPapers && oldPapers.length) {
            let oldPaper = oldPapers.find((paper: any) => paper?.year == year);
            if (oldPaper) {
                return (
                    oldPaper[sem] &&
                    Array.isArray(oldPaper[sem]) &&
                    oldPaper[sem].length > 0
                ) ? (oldPaper[sem].filter((val: any) => viewAll || currentSubjects.includes(val?.subject_id))) : undefined;
            } else { return false }
        } else {
            return undefined;
        }
    }

    return <div>
        <div className="pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
            {
                Array.isArray(course?.tags) && course?.tags.map((tag: any, ind: any) =>
                    <span onClick={() => setCurrentTag(tag?.id)} className={"inline-block rounded-full px-3 py-1 text-sm font-semibold mr-2 mt-2 cursor-pointer " + ((tag?.id === currentTag) ? "bg-gray-700 text-gray-200" : "text-gray-700 bg-gray-200")}>
                        <Link to={`/course/${course?.id}/${tag?.id}`}>
                            {tag?.title}
                        </Link>
                    </span>
                )
            }
        </div>
        {
            currentTag !== undefined &&
            <div className="pt-2">
                <div className="flex">
                    <div className="grow"><h1 className="mt-2 order-1 align self-end text-gray-900 text-lg font-bold tracking-tight">Subject </h1></div>
                    <div className="flex-none">
                        <span className="order-1 align align-baseline text-gray-700 text-sm">View All</span> <Checkbox onClick={() => changeViewAll()} checked={viewAll} defaultChecked size="small" /> </div>
                </div>

                <div className=" pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
                    {
                        currentTag && getCurrentTag() && Array.isArray(getCurrentTag()?.subjects) && getCurrentTag()?.subjects.map((subject: any, ind: any) =>
                            <span onClick={() => changeSubject(currentTag, subject?.id)} className={"inline-block rounded-full px-3 py-1 text-sm font-semibold mr-2 mt-2 cursor-pointer " + ((currentSubjects.includes(subject?.id) || viewAll) ? "bg-gray-700 text-gray-200" : "text-gray-700 bg-gray-200")}>
                                {subject?.short_name}
                            </span>
                        )
                    }
                </div>
            </div>

        }

        <div className="mt-3 pt-1 max-w-md rounded overflow-hidden shadow-md border-t-2 border-slate-300 border-1">
            <h1 className="px-2 order-1 text-gray-900 text-base font-bold tracking-tight">Syllabus</h1>
            {
                currentTag && getCurrentTag() && getCurrentTag()?.syllabus && getSyllabus().length ?
                    <div className="pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
                        <List sx={{ width: '100%', maxWidth: 360 }} dense>
                            {getSyllabus().map((syllabus: any, ind: any) =>
                                <ListItem key={ind} secondaryAction={
                                    <div>
                                        {
                                            (syllabus?.link !== undefined && syllabus?.link !== "") &&
                                            <a href={syllabus?.link}>
                                                <IconButton size="small" edge="end" aria-label="delete"> <Download /> </IconButton>
                                            </a>
                                        }
                                        {/* <IconButton size="small" edge="end" aria-label="delete"> <Visibility /> </IconButton> */}
                                    </div>
                                }>
                                    <ListItemText primary={syllabus?.name} secondary={`${syllabus?.code} - ${syllabus?.short_name}`} />
                                </ListItem>
                            )}
                        </List>
                    </div>
                    : <h1 className="px-2 py-2 text-center text-gray-700">No found.</h1>
            }
        </div>

        <div>
            <h1 className="px-2 pt-3 order-1 text-gray-900 text-base font-bold tracking-tight">Old Papers</h1>
            <div className="mt-3 pt-1 max-w-md rounded overflow-hidden shadow-md border-t-2 border-slate-300 border-1">
                {
                    currentTag && getCurrentTag() && getCurrentTag()?.syllabus && getOldPapers().length ?
                        <div className="pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
                            {getOldPapers().map((old_paper: any, ind: any) =>
                                <div key={ind}>
                                    <OldPaperCard papers={getOldYearPapers(old_paper?.year, "winter")} year={old_paper?.year} sem="Winter" />
                                    <OldPaperCard papers={getOldYearPapers(old_paper?.year, "summer")} year={old_paper?.year} sem="Summer" />
                                </div>
                            )}
                        </div>
                        : <h1 className="px-2 py-2 text-center text-gray-700">No found.</h1>
                }
            </div>
        </div>

        <div>
            <h1 className="px-2 pt-3 order-1 text-gray-900 text-base font-bold tracking-tight">Books </h1>
            <div className="pt-2 pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
                <div>

                    {
                        (currentTag && getCurrentTag() && getCurrentTag()?.books) ?
                            getBooks().map((book: any, ind: any) => {
                                return <div className="w-full flex p-2" key={ind}>
                                    <div className="flex-none pdf-image shadow-md rounded" style={{ backgroundImage: `url("${book.image}")` }}>
                                    </div>
                                    <div className="flex-2 whitespace-normal px-2">
                                        <h1 className="text-gray-900 text-base font-bold">{book?.name}</h1>
                                        <Button size="small" variant="contained">Download</Button>
                                    </div>
                                </div>
                            }) : <React.Fragment></React.Fragment>
                    }

                    {/* <a href="#" className="flex flex-col items-center bg-white rounded-lg border shadow-md md:flex-row md:max-w-xl hover:bg-gray-100 dark:border-gray-700 dark:bg-gray-800 dark:hover:bg-gray-700">
                        <img className="object-cover w-full h-96 rounded-t-lg md:h-auto md:w-48 md:rounded-none md:rounded-l-lg" src="https://1.bp.blogspot.com/-_7125Zu3LkM/YDSHijvglQI/AAAAAAAAANU/l9LhJO49LL0q43XFutiaSY1ILsaW7fkeQCNcBGAsYHQ/s367/Screenshot_435.png" />
                        <div className="flex flex-col justify-between p-4 leading-normal">
                        <h5 className ="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">Noteworthy technology acquisitions 2021</h5>
                        <p className ="mb-3 font-normal text-gray-700 dark:text-gray-400">Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order.</p>
                        </div>
                    </a> */}
                </div>
            </div>
        </div>
        <div className="pt-2 pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
            <h1 className="order-1 text-gray-900 text-base font-bold tracking-tight">IMP Material </h1>
        </div>
        <div className="pt-2 pb-3 text-nowrap" style={{ maxWidth: "calc(100vw - 2rem)" }}>
            <h1 className="order-1 text-gray-900 text-base font-bold tracking-tight">Paper Solution </h1>
        </div>

    </div >
};



class Course extends React.Component<Iprops, IState, Ipage> {

    constructor(props: Iprops) {
        super(props);

        this.state = {
            is_loading: false,
            id: props.match.params.course,
            course: undefined
        }
        this.loadCourse();
    }

    loadCourse = async () => {
        this.setState({ is_loading: true });
        let course = await CourseService.getOne(this.state.id)
        this.setState({ course, is_loading: false });
    }

    render() {
        return (
            <div>
                <div className="p-4 flex">
                    <div className="grow">
                        <h1 className="order-1 text-gray-900 text-3xl font-extrabold tracking-tight mt-2">GTU Material</h1>
                        <p className="max-w-2xl text-sm text-gray-500"> Syllabus, Books, Old Papers, IMP Material, Paper Solution.... </p>
                    </div>
                    <div className="grow-0 self-center">
                        <Link to={"/"}>
                            <img src={ApyLogoDark} alt="" className="cursor-pointer h-16 header-icon rounded-lg" />
                        </Link>
                    </div>
                </div>

                <div className="px-4">
                    <div className="grow">
                        <h1 className="order-1 text-gray-900 text-xl font-extrabold tracking-tight mt-2">{this.state.course?.title}</h1>
                    </div>
                    <CourseCard course={this.state.course} />
                </div>

            </div>
        );
    }
}
export default Course
