import Dashboard from '../pages/Dashboard';
import Course from '../pages/Course';
import IRoute from './../interfaces/route';

const routes: IRoute[] = [
    {
        path: '/',
        name: 'Dashboard',
        exact: true,
        components: Dashboard,
    },
    {
        path: '/course/:course',
        name: 'Course',
        exact: true,
        components: Course,
    },
    {
        path: '/course/:course/:tag',
        name: 'Course',
        exact: true,
        components: Course,
    },
    {
        path: '/course/:course/:tag/:subject',
        name: 'Course',
        exact: true,
        components: Course,
    },
]

export default routes;