import React, { useEffect, Component, ReactElement, FC, useState } from 'react';
import ReactDOM from "react-dom";
import './../App.css';
import { Link } from "react-router-dom";
import Ipage from '../interfaces/page';
import logging from '../config/logging';
import ApyLogoDark from "./../assets/apy-logo-dark.png";
import CourseService from '../services/course';

interface Iprops {
}
interface IState {
    is_loading: Boolean,
    values: any
};

type ChildProps = {
    course: any | {
        id: string;
        title: string;
        tags: any;
    }
}

const CourseCard: FC<ChildProps> = ({ course }): ReactElement => {

    const [currentTag, setCurrentTag] = useState(undefined);

    const getCurrentTag = () => {
        return course?.tags.find((val: any) => val?.id == currentTag);
    }

    return <div className="max-w-md rounded overflow-hidden shadow-md border-t-2 border-slate-300 border-1">
        <div className="px-6 pt-3">
            <div className="font-bold text-xl mb-2">{course?.title}</div>
        </div>
        <div className="px-6 pb-3">
            {
                Array.isArray(course?.tags) && course?.tags.map((tag: any, ind: any) =>
                    <span
                        onClick={() => setCurrentTag(tag?.id)}
                        className={((tag?.id === currentTag && currentTag !== undefined) ? "bg-gray-700 text-gray-200" : "text-gray-700 bg-gray-200") + " inline-block rounded-full px-3 py-1 text-sm font-semibold mr-2 mt-2 cursor-pointer "}>
                        {tag?.title}
                    </span>
                )
            }
        </div>
        {
            currentTag !== undefined &&
            <div className="px-6 pt-2 pb-3 bg-slate-800">
                {
                    currentTag && getCurrentTag() && Array.isArray(getCurrentTag()?.subjects) && getCurrentTag()?.subjects.map((subject: any, ind: any) =>
                        <span className={"inline-block rounded-full px-3 py-1 text-sm font-semibold mr-2 mt-2 cursor-pointer text-gray-700 bg-gray-200"}>
                            <Link to={`/course/${course?.id}/${currentTag}/${subject?.id}`}>
                                {subject?.short_name}
                            </Link>
                        </span>
                    )
                }
            </div>
        }

    </div>
};



class Dashboard extends React.Component<Iprops, IState, Ipage> {

    constructor(props: Iprops) {
        super(props);

        this.state = {
            is_loading: false,
            values: []
        }
        this.loadCourses();
    }

    loadCourses = async () => {
        this.setState({ is_loading: true });
        let values = await CourseService.getAll()
        values.sort((a: any, b: any) => a.title.localeCompare(b.title));

        values = values.map((value: any) => {
            value.order = value.order ? value.order : values.length
            return value;
        })
        values.sort((a: any, b: any) => a.order - b.order);

        this.setState({ is_loading: false, values });
    }

    render() {
        return (
            <div>
                <div className="p-4 flex">
                    <div className="grow">
                        <h1 className="order-1 text-gray-900 text-3xl font-extrabold tracking-tight mt-2">GTU Material</h1>
                        <p className="max-w-2xl text-sm text-gray-500"> Syllabus, Books, Old Papers, IMP Material, Paper Solution.... </p>
                    </div>
                    <div className="grow-0 self-center">
                        <Link to={"/"}>
                            <img src={ApyLogoDark} alt="" className="cursor-pointer h-16 header-icon rounded-lg" />
                        </Link>
                    </div>
                </div>

                <div className="grid grid-cols-1 gap-5 p-3">
                    {
                        Array.isArray(this.state.values) && this.state.values.map((val: any, ind: any) => {
                            return <div className="mx-auto" key={ind}>
                                <CourseCard course={val} />
                            </div>
                        })
                    }
                </div>

            </div>
        );
    }
}
export default Dashboard
